/* Basscss Typography */
.font-family-inherit {
  font-family: inherit; }

.font-size-inherit {
  font-size: inherit; }

.text-decoration-none {
  text-decoration: none; }

.bold {
  font-weight: bold; }

.regular {
  font-weight: normal; }

.italic {
  font-style: italic; }

.caps {
  text-transform: uppercase;
  letter-spacing: 0.2em; }

.left-align {
  text-align: left; }

.center {
  text-align: center; }

.right-align {
  text-align: right; }

.justify {
  text-align: justify; }

.nowrap {
  white-space: nowrap; }

.break-word {
  word-wrap: break-word; }

.line-height-1 {
  line-height: 1; }

.line-height-2 {
  line-height: 1.125; }

.line-height-3 {
  line-height: 1.25; }

.line-height-4 {
  line-height: 1.5; }

.list-style-none {
  list-style: none; }

.underline {
  text-decoration: underline; }

.truncate {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.list-reset {
  list-style: none;
  padding-left: 0; }

body {
  background: #f0f0f0;
  font-family: 'Source Sans Pro', sans-serif; }

.text-cursive {
  font-family: 'Pacifico', cursive; }

.container {
  background: #fff;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto; }

.top {
  background: #001f3f;
  color: #fff;
  padding: 20px; }

.city {
  font-size: 40px; }

.current-weather .label {
  opacity: 0.5;
  font-weight: 300;
  font-size: 13px;
  display: inline-block; }

.item {
  border-bottom: 1px solid #ddd;
  padding: 20px; }

.day {
  text-transform: uppercase;
  color: #999;
  margin-right: 10px;
  margin-bottom: 10px; }

.weather-icon {
  font-size: 30px; }

.meta {
  padding-right: 10px;
  text-align: center;
  display: flex; }
  .meta .wi {
    margin-right: 2px; }

.meta-temp .wi {
  font-size: 14px; }

.meta-result span {
  font-size: 11px;
  opacity: 0.5;
  padding-left: 2px; }

.wi-wind {
  font-size: 22px;
  margin-right: 2px; }

.current-weather .meta {
  font-size: 24px; }

.current-weather .meta-temp .wi {
  position: relative;
  top: -2px; }
